@mixin mobile{
    @media screen and (max-width:480px) {
        @content;
    }
}

@mixin tablet {
    @media screen and (max-width:768px) {
        @content;
    }
}

@mixin laptop{
    @media screen and (max-width:1200px) {
        @content;
    }
}
.formConatiner
{
    background-color: #a7bcff;
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;

    .formWrapper
    {
        background-color: white;
        padding: 20px 60px;
        border-radius: 10px;
        display: flex;
        flex-direction: column;
        gap: 10px;
        align-items: center;

        .logo{
            color: #5d5b8d;
            font-weight: bold;
            font-size: 24px;
        }

        .title{
            color: #5d5b8d;
            font-size: 14px;
            font-weight: 400;
        }

        form{
            display: flex;
            flex-direction: column;
            gap: 15px;

            input{
                width: 250px;
                padding: 15px;
                border: none;
                border-bottom: 1px solid #a7bcff;

                &::placeholder{
                    color:  rgb(175,175,175);
                }
            }

            button{
                background-color: #a7bcff;
                color: white;
                padding: 10px;
                font-weight: bold;
                border: none;
                cursor: pointer;
            }

            label{
                display: flex;
                align-items: center;
                gap: 10px;
                color: #8da4f1;
                font-size: 12px;
                cursor: pointer;

                img{
                    width: 32px;
                }
            }
        }

        p{
            color: #5d5b8d;
            font-size: 12px;
            margin-top: 10px;
        }
    }
}

.home
{
    background-color: #a7bcff;
    height: 100vh;
    display:  flex;
    align-items: center;
    justify-content: center;

    .container
    {
        border:  1px solid white;
        border-radius: 10px;
        width: 65%;
        height: 80%;
        display: flex;
        overflow: hidden;
        @include tablet{
            width: 90%;
        }

        .sidebar
        {
            flex: 1;
            border-right: 1px solid #5d5b8d;
            background-color: #504e7a;
            position: relative;

            .navbar
            {
                display: flex;
                align-items: center;
                background-color: #2f2d52;
                height: 50px;
                padding: 10px;
                justify-content: space-between;
                color: #ddddf7;

                .logo
                {
                    font-weight: bold;
                    @include tablet{
                        display: none;
                    }
                }
                .user
                {
                    display: flex;
                    gap: 10px;
                    
                    img
                    {
                        background-color: #ddddf7;
                        height: 24px;
                        width: 24px;
                        border-radius: 50%;
                        object-fit: cover;
                    }

                    button
                    {
                        background-color: #5d5b8d;
                        color: #ddddf7;
                        font-size: 10px;
                        border: none;
                        cursor: pointer;
                        @include tablet{
                            position: absolute;
                            bottom: 10px;
                        }
                    }
                }
            }
        }

        .search
        {
            border-bottom: 1px solid gray;

            .searchForm
            {
                padding: 10px;
                input
                {
                    background-color: transparent;
                    border: none;
                    color: white;
                    outline: none;

                    &::placeholder
                    {
                        color: lightgray;
                    }
                }
            }

           
        }
        .userChat
        {
            padding: 10px;
            display: flex;
            align-items: center;
            gap: 10px;
            color: white;
            cursor: pointer;

            &:hover{
                background-color: #2f2d52;
            }
            img
            {
                width: 50px;
                height: 50px;
                border-radius: 50%;
                object-fit: cover;
            }

            .userChatInfo
            {
                span{
                    font-size: 18px;
                    font-weight: 500;
                }

                p
                {
                    font-size: 14px;
                    color: lightgray;
                }
            }
        }
        .chat
        {
            flex: 2;

            .chatInfo
            {
                height: 50px;
                background-color: #5d5b8d;
                display: flex;
                align-items: center;
                justify-content: space-between;
                padding: 10px;
                color: lightgray;
            }
            .chatIcons
            {
                display: flex;
                gap: 10px;

                img
                {
                    height: 24px;
                    cursor: pointer;
                }
            }

            .messages
            {
                background-color: #ddddf7;
                padding: 10px;
                height:  calc(100% - 160px);
                overflow: scroll;
                scroll-behavior: smooth;
                .message
                {
                    display: flex;
                    gap: 10px;
                    margin-bottom: 20px;

                    .messageInfo
                    {
                        display: flex;
                        flex-direction: column;
                        color: gray;
                        font-weight: 300;
                        img
                        {
                            width: 40px;
                            height: 40px;
                            border-radius: 50%;
                            object-fit: cover;
                        }
                    }

                    .messageContent
                    {
                        max-width: 80%;
                        display: flex;
                        flex-direction: column;
                        gap: 10px;

                        p
                        {
                            background-color: white;
                            padding: 10px 20px;
                            border-radius: 0px 10px 10px 10px;
                            max-width: max-content ;
                        }
                        img
                        {
                            width: 50%;
                        }
                    }

                    &.owner
                    {
                        flex-direction: row-reverse;

                       .messageContent
                       {
                            align-items: flex-end;
                            p
                            {
                                background-color: #8da4f1;
                                color: white;
                                border-radius: 10px 0px 10px 10px ;
                            }
                       }
                    }
                }
            }

            .input
            {
                height: 50px;
                background-color: white;
                padding: 10px;
                display: flex;
                align-items: center;
                justify-content: space-between;

                input
                {
                    border: none;
                    outline: none;
                    width: 100%;
                    color: #2f2d52;
                    font-size: 18px;

                    &::placeholder{
                        color: rgb(168, 166, 166);
                    }
                }

                .send
                {
                    display: flex;
                    align-items: center;
                    gap: 10px;

                    img
                    {
                        height: 24px;
                        cursor: pointer;
                    }

                    button
                    {
                        border: none;
                        padding: 10px 15px;
                        color: white;
                        background-color: #8da4f1;
                        cursor: pointer;
                    }
                }
            }
        }
    }
}